import { createServerClient, type CookieOptions } from '@supabase/ssr'
import { NextResponse, type NextRequest } from 'next/server'
import { getSupabaseEnv, isSupabaseConfigured } from '@/lib/supabase/env'

export async function proxy(request: NextRequest) {
  let response = NextResponse.next({
    request: {
      headers: request.headers,
    },
  })

  if (!isSupabaseConfigured()) {
    return response
  }

  const { url, anonKey } = getSupabaseEnv()

  const supabase = createServerClient(url, anonKey, {
    cookies: {
      get(name: string) {
        return request.cookies.get(name)?.value
      },
      set(name: string, value: string, options: CookieOptions) {
        request.cookies.set({ name, value, ...options })
        response = NextResponse.next({
          request: { headers: request.headers },
        })
        response.cookies.set({ name, value, ...options })
      },
      remove(name: string, options: CookieOptions) {
        request.cookies.set({ name, value: '', ...options })
        response = NextResponse.next({
          request: { headers: request.headers },
        })
        response.cookies.set({ name, value: '', ...options })
      },
    },
  })

  const {
    data: { session },
  } = await supabase.auth.getSession()
  const pathname = request.nextUrl.pathname

  const authRoutes = ['/login', '/registro']
  const isAuthRoute = authRoutes.some((route) => pathname === route)

  const isOnboardingRoute = pathname.startsWith('/onboarding')

  const protectedRoutes = ['/unirse', '/equipos', '/equipo']
  const isProtectedRoute = protectedRoutes.some((route) => pathname.startsWith(route))

  if (!session) {
    if (isAuthRoute) {
      return response
    }
    if (isProtectedRoute || isOnboardingRoute) {
      return NextResponse.redirect(new URL('/login', request.url))
    }
    return response
  }

  const { data: perfil } = await supabase
    .from('perfiles')
    .select('nombre, genero, edad, peso_kg, altura_cm, perfil_completo')
    .eq('id', session.user.id)
    .single()

  const perfilCompleto =
    perfil &&
    (perfil.perfil_completo === true ||
      (perfil.nombre &&
        perfil.genero &&
        perfil.edad &&
        perfil.peso_kg &&
        perfil.altura_cm))

  if (isAuthRoute) {
    if (perfilCompleto) {
      return NextResponse.redirect(new URL('/equipos', request.url))
    }
    return NextResponse.redirect(new URL('/onboarding/nombre', request.url))
  }

  if (!perfilCompleto) {
    if (isOnboardingRoute) {
      return response
    }
    if (isProtectedRoute) {
      return NextResponse.redirect(new URL('/onboarding/nombre', request.url))
    }
  }

  if (perfilCompleto && isOnboardingRoute) {
    return NextResponse.redirect(new URL('/equipos', request.url))
  }

  return response
}

export const config = {
  matcher: [
    '/login',
    '/registro',
    '/unirse',
    '/equipos',
    '/equipos/:path*',
    '/equipo/:path*',
    '/onboarding/:path*',
  ],
}
